* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.img_box {
  height: 120px;
  width: 130px;
  border-radius: 50%;
}
.img_box img {
  height: 100%;
  width: 100%;
  border-radius: 50%;
  box-shadow: 0 10px 10px #dbd3c3;
}
.img_profile {
  margin-left: 10px;
}

* {
  margin: 0;
  padding: 0;
  font-family: "poppins";
  box-sizing: border-box;
}
.blog_container {
  background: cadetblue;
  position: relative;
}
.form_container {
  /* width: 300px; */
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.12);
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
}
.showblog {
  height: 100vh;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 10;
  background: rgba(0, 0, 0, 0.6);
}
.img_sec {
  height: 40vh;
  width: 100%;
}
.img_sec img {
  height: 100%;
  width: 100%;
  object-fit: fill;
}

@media screen and (max-width: 768px) {
  .form_container {
    width: 600px;
  }
  .form_blog_container {
    -webkit-flex-direction: column;
            flex-direction: column;
  }
}

.Contact {
  width: 100%;
  height: 100vh;
  background: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4));
  background-size: cover;
}

.contact_avatar {
  width: 50px;
  height: 50px;
  overflow: hidden;
  border-radius: 50%;
}

.contact_avatar img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.contact_social_handles p {
  color: black;
  text-decoration: none;
  font-size: 30px;
}

.ContactCard {
  background: rgba(255, 255, 255, 0.3);
  padding: 40px 40px;
  -webkit-backdrop-filter: blur(12px);
          backdrop-filter: blur(12px);
}

